import * as api from "@js/networking/Api.web";



const SupplementFormKey = 'supplementFormKey';
const SupplementImageUploadKey = 'supplementImageUploadKey';
const SupplementMutImageUploadKey = 'supplementMutImageUploadKey';



let MutImageSelectBox = class extends React.Component {
    render() {
        return (
            <MutImageSelect {...this.props}/>
        )
    }
};

MutImageSelectBox = redux_upload({
    upload: SupplementMutImageUploadKey,
    uploadApi: api.uploadImageWithPost,
    getUUIDFuc: ((model) => model.uuid),
    isSuccess: ((model) => model.success)
})(MutImageSelectBox);



let ImageSelectBox = class extends React.Component {
    render() {
        return (
            <ImageSelect {...this.props}/>
        )
    }
};

ImageSelectBox = redux_upload({
    upload: SupplementImageUploadKey,
    uploadApi: api.uploadImageWithPost,
    getUUIDFuc: ((model) => model.uuid),
    isSuccess: ((model) => model.success)
})(ImageSelectBox);




let UploadLoading = class extends Component {
    render() {
        return (
            <ActivityIndicator
                toast
                animating={this.props.show}
                text={this.props.totalCount > 0 ? `${this.props.uploadedCount}/${this.props.totalCount}` : ''}
            />
        )
    }
};

UploadLoading = connect(
    (state) => {
        const uploadedCount = getUploadedCount(state, SupplementImageUploadKey) + getUploadedCount(state, SupplementMutImageUploadKey);
        const wattingCount = getWattingCount(state, SupplementImageUploadKey) + getWattingCount(state, SupplementMutImageUploadKey);
        const failedCount = getFailedCount(state, SupplementImageUploadKey) + getFailedCount(state, SupplementMutImageUploadKey);
        const totalCount = uploadedCount + wattingCount + failedCount;
        return {
            uploadedCount: uploadedCount,
            totalCount: totalCount,
            uploading: true,
        }
    },
    null,
)(UploadLoading);




let NextButton = class extends React.Component {

    uploadCompletesub: any;

    constructor() {
        super(...arguments)
    }

    componentDidMount() {
        this.uploadCompletesub = UploadEventEmitter.addCompleteListener((upload) => {
            if (upload === SupplementMutImageUploadKey) {
                this.props.startUploadImage()           //先上传补充资料的图片，再上传消费凭证的图片
            } else {
                if (this.props.uploadSuccess) {
                    const {handleSubmit} = this.props;
                    //因为不是事件触发的提交 handleSubmit(this._submit) 写法不会执行_submit函数，所以将_submit改为立即执行函数就会执行了
                    handleSubmit(this._submit)()
                } else if (this.props.failedCount !== 0) {
                    this.props.formStopSubmit({_error: '图片上传失败'});
                    Toast.info('图片上传失败', 1)
                }
            }
        })

    }

    componentWillUnmount() {
        UploadEventEmitter.removeSubscription(this.uploadCompletesub);
        this.props.formStopSubmit({_error: '提交失败'}) //在上传过程中点返回停止提交
    }

    _onPress = () => {
        this.props.formStartSubmit();
        this._startCompress()
    };

    _startCompress = () => {
        const newQueue = this.props.mutImageUploadObj.wattingQueue;
        const wattingSize = newQueue.size;

        if (wattingSize === 0) {
            this.props.startUploadImage();   //如果补充资料没选图片，那么直接执行消费凭证的图片上传
            return
        }

        let compressedCount = 0;
        for (let item of newQueue.values()) {    //遍历补充资料中的图片且压缩
            lrz(item.filepath, {width: 1000, height: 1000, quality: 1,})
                .then((rst) => {
                    this.props.deleteUploadItemMutImage(item.name);    //先删除当前旧的图片
                    this.props.pushUploadMutImage(rst.base64, item.name);   //再将当前新的图片添加到等待上传队列
                    compressedCount++
                    if (compressedCount === wattingSize) {
                        //success
                        this.props.startUploadMutImage()
                    }
                })
                .catch((error) => {
                    Toast.info('图片压缩失败', 1);
                    this.props.formStopSubmit({_error: '图片压缩失败'})
                })
        }
    }

    _submit = (() => {
        let submitting = false;
        const complete = () => submitting = false;

        return (values) => {
            if (submitting) return;
            submitting = true;
            //提交资料之后跳合同
            this._commit(values)
                .then((model) => {
                    complete();
                    this.props.formStopSubmit(null);
                    Toast.info(model.msg, 2);
                })
                .catch((error) => {
                    complete();
                    this.props.formStopSubmit({_error: '提交失败'})
                })

        }
    })();

    _commit = async (values) => {
        const userId = await window.GoldWeb.getUserId();
        let material_data = {};
        const imgIDs = getUpdatedIDs(this.props.mutImageUploadObj).join(",");   //获取补充资料的图片
        const remark = values.supplementExplain || '';
        material_data.imgIDs = imgIDs;
        material_data.remark = remark;
        material_data.xfFile = getUpdateItemID(this.props.imageUploadObj, 'xfFile');   //获取消费凭证的图片
        material_data.address = values.address;
        console.log(material_data);
        const model = api.uploadSupplement(userId, JSON.stringify(material_data));
        console.log(model);
        return model;
    };

    render() {

        const {valid} = this.props;

        return (
            <ButtonBox enable={valid} onClick={this._onPress}>立即登录</ButtonBox>
        )
    }
};

NextButton = connect(
    (state) => {
        const uploadedCount = getUploadedCount(state, SupplementImageUploadKey) + getUploadedCount(state, SupplementMutImageUploadKey);
        const wattingCount = getWattingCount(state, SupplementImageUploadKey) + getWattingCount(state, SupplementMutImageUploadKey);
        const failedCount = getFailedCount(state, SupplementImageUploadKey) + getFailedCount(state, SupplementMutImageUploadKey);
        const totalCount = uploadedCount + wattingCount + failedCount;
        return {
            uploadSuccess: hasAllsccess(state, SupplementImageUploadKey) && hasAllsccess(state, SupplementMutImageUploadKey),
            imageUploadObj: getUploadData(state, SupplementImageUploadKey),
            mutImageUploadObj: getUploadData(state, SupplementMutImageUploadKey),
            uploadedCount: uploadedCount,
            totalCount: totalCount,
            failedCount: failedCount,
        }
    },
    (dispatch) => ({
        formStartSubmit: () => dispatch(startSubmit(SupplementFormKey)),
        formStopSubmit: (error) => dispatch(stopSubmit(SupplementFormKey, error)),
        startUploadMutImage: () => dispatch(upload(SupplementMutImageUploadKey)),
        startUploadImage: () => dispatch(upload(SupplementImageUploadKey)),
        deleteUploadItemMutImage: (name) => dispatch(deleteUploadItem({
            upload: SupplementMutImageUploadKey,
            name: name
        })),
        pushUploadMutImage: (filePath, name) => dispatch(pushUploadItem({
            upload: SupplementMutImageUploadKey,
            filePath: filePath,
            name: name
        })),
    })
)(NextButton)





