const validateForm = (value) => {
    const error = {};
    error.address = required(value.address);
    error.xfFile = required(value.xfFile);
    return error;
};


SupplementData = reduxForm({
    form: SupplementFormKey,
    validate: validateForm
})(SupplementData);

export default SupplementData;



