import React from 'react'

import styled from 'styled-components'

import {reduxForm} from 'redux-form'

import {
    VCCodeField
} from '@js/component/index.web'


class CDPage extends React.Component {

    _sendCode = () => {
        return Promise.resolve({result: true})
    };

    render() {
        return(
            <Root>
                <VCCodeField
                    name={'smsCode'}
                    placeholder={'请输入验证码'}
                    cdKey={'LoginVercodeCD'}
                    enable={true}
                    onClick={this._sendCode}
                />
            </Root>
        )
    }
}

export default reduxForm({
    form: 'cdPage'
})(CDPage);

const Root = styled.div`
    width: 100%;
`;


