import React, {Component} from 'react';
import PropTypes from 'prop-types'
import styled from 'styled-components'
import {connect} from 'react-redux'
import lrz from "lrz";
import {change, getFormValues, registerField, unregisterField} from "redux-form";
import {Flex, ImagePicker,} from 'antd-mobile';

let FormKey = '';

class ImageSelect extends Component {

    static propTypes = {
        formKey: PropTypes.string,
        image: PropTypes.any,
        name: PropTypes.string,
        imagename: PropTypes.string,
        pushUploadItem: PropTypes.func,
        deleteUploadItem: PropTypes.func
    };

    constructor() {
        super(...arguments);
        this.state = {
            singleFile: [],
        };
        FormKey = this.props.formKey;
    }

    componentDidMount() {
        this.props.registerField(this.props.name)
    }

    componentWillUnmount() {
        this.props.unregisterField(this.props.name)
    }

    _onChangesdFile = (files, type, index) => {
        this.setState({
            singleFile: files
        })

        if (type === 'add') {
            lrz(files[0].url, {width: 1000, height: 1000, quality: 1,})
                .then(
                    (rst) => {
                        const {name} = this.props;
                        this.props.change(name, rst.base64);
                        this.props.deleteUploadItem(name);             //先删除当前旧的图片
                        this.props.pushUploadItem(rst.base64, name);   //再将当前新的图片添加到等待上传队列
                    }
                )
                .catch((error) => {
                    alert('图片压缩失败，请重试');
                    this.setState({
                        singleFile: []
                    })
                })
        } else {
            const {name} = this.props;
            this.props.change(name, null);
            this.props.deleteUploadItem(name)
        }
    }

    render() {
        const {imagename, image} = this.props;
        console.log('uri===', this.props.uri);

        return (
            <UploadBox image={image}>
                <div style={{float: 'right', paddingTop: '0.2rem', paddingBottom: '0.2rem'}} className="edmoney_infosure_imageUpload">
                    <ImagePicker
                        style={{width: '1rem', height: '1rem'}}
                        files={this.state.singleFile}
                        selectable={this.state.singleFile.length < 1}
                        onChange={this._onChangesdFile}
                        onImageClick={(index, fs) => {
                            console.log(index, fs)
                        }}
                        multiple={false}
                    />
                    <TitleP>{imagename}</TitleP>
                </div>
            </UploadBox>
        )
    }
}

ImageSelect = connect(
    (state, props) => {
        const values = getFormValues(FormKey)(state) || {};
        return {
            uri: values[props.name]
        }
    },
    (dispatch) => ({
        change: (name, uri) => dispatch(change(FormKey, name, uri)),
        registerField: (name) => dispatch(registerField(FormKey, name, 'Field')),
        unregisterField: (name) => dispatch(unregisterField(FormKey, name)),
    })
)(ImageSelect);

export default ImageSelect

const RowFlex = styled(Flex).attrs({
    direction: 'row',
    align: 'center',
})`
  width: 100%;
`;

const UploadBox = styled(RowFlex).attrs({
    justify: 'between',
})`
    height: 1.76rem !important;
    padding-right: 0.16rem;
    div.am-flexbox-align-center {
        width:1rem;height:1rem;
    };
    div.am-image-picker-upload-btn:before{
        width:1rem;
        height:1rem;
        border: 0;
        background-image: ${props => props.image};
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100%;
        background-color: #fff;
    }
    div.am-image-picker-upload-btn:after{
        border:0;
        background-color: #fff;
    }
    div.am-image-picker-list{padding:0;margin:0}
    div.am-flexbox-item:first{
        width:1rem;
        height:1rem;
        position: absolute;
        top: 0.2rem;
        left: 0;
        z-index: 100;
    }
    div.am-image-picker-item{width:1rem;height:1rem;}

`;

const TitleP = styled.p`
    font-size: 0.24rem;
    color: #999999;
    text-align: center;
    margin-top: 0.1rem;
`;


