import React from 'react'

import styled from 'styled-components'

import {connect} from 'react-redux'

import {reduxForm, formValueSelector} from 'redux-form'

import {ActivityIndicator, Toast} from 'antd-mobile'

import {
    Button,
    InputItemField,
    PasswordField,
    PickerField,
    TextAreaField,
    RadioField,
    CheckboxField,
} from '@js/component/index.web';

import {
    required,
    regName,
    phoneNumber,
    checkLuhm,
} from '@js/component/form/Validafunctions';

import {
    requestAccountData
} from './reducers/AccountActions'


const seasons = [
    {
        label: '中国农业银行',
        value: 'ABC',
    },
    {
        label: '交通银行',
        value: 'BCM',
    },
    {
        label: '中国银行',
        value: 'BOC',
    },
    {
        label: '中国建设银行',
        value: 'CCB',
    },
    {
        label: '中国光大银行',
        value: 'CEB',
    },
    {
        label: '兴业银行',
        value: 'CIB',
    },
    {
        label: '中信银行',
        value: 'CITIC',
    },
    {
        label: '招商银行',
        value: 'CMB',
    },
    {
        label: '中国民生银行',
        value: 'CMBC',
    },
    {
        label: '广东发展银行',
        value: 'GDB',
    },
    {
        label: '华夏银行',
        value: 'HXB',
    },
    {
        label: '中国工商银行',
        value: 'ICBC',
    },
    {
        label: '平安银行',
        value: 'PAB',
    },
    {
        label: '中国邮政储蓄银行',
        value: 'PSBC',
    },
    {
        label: '上海浦东发展银行',
        value: 'SPDB',
    },
];

const sleep = ms => new Promise(resolve => setTimeout(resolve, ms));

const selector = formValueSelector('formData');

class FormItem extends React.Component {

    constructor(props){
        super(props)
    }

    render() {
        const {realname, mobile, bankNum} = this.props;

        return(
            <FormBox>
                <Label>请输入姓名</Label>
                <InputItemField
                    name={'realname'}
                    height={'.88rem'}
                    //validate={realname && [regName]}
                />
                <Label>性别</Label>
                <RadioField
                    name={'sex'}
                    radioData={[{value: 1, label: '男'},{value: 2, label: '女'}]}
                />
                <Label>兴趣爱好</Label>
                <CheckboxField
                    name={'hobby'}
                    checkboxData={
                        [
                            {value: 0,label:'篮球'},
                            {value: 1,label:'游泳'},
                            {value: 2,label:'唱歌'},
                            {value: 3,label:'旅游'}]
                    }
                />
                <Label>请输入您的手机号</Label>
                <InputItemField
                    name={'mobile'}
                    height={'.88rem'}
                    validate={mobile && [phoneNumber]}
                />
                <Label>请输入您的银行卡号</Label>
                <InputItemField
                    name={'bankNum'}
                    height={'.88rem'}
                    validate={bankNum && [checkLuhm]}
                />
                <Label>银行类型</Label>
                <PickerField
                    name={'grade'}
                    pickerData={seasons}
                    extraValue={'请选择您的银行类型'}
                />
                <Label>设置密码</Label>
                <PasswordField
                    name={'password'}
                />
                <Label>详细地址</Label>
                <TextAreaField
                    name={'textarea'}
                    height={'1.5rem'}
                />
            </FormBox>
        )
    }
}

FormItem = connect(
    (state) => ({
        realname: selector(state, 'realname'),
        mobile: selector(state, 'mobile'),
        bankNum: selector(state, 'bankNum'),
    })
)(FormItem);

class FormData extends React.Component {

    _nextFun = async (values) => {
        sleep(1000);
        //const {mobile, bankNum} = values;
        console.log(values);
        const userId = await window.GoldWeb.getUserId();
        return requestAccountData(values, userId)
            .then((model) => {
                Toast.info(model.msg);
                console.log(model);
            })
            .catch(error => {})
    };

    render(){
        const {handleSubmit, valid, submitting} = this.props;

        return(
            <Root>
                <FormItem {...this.props}/>

                <ActivityIndicator
                    toast
                    text={'正在提交...'}
                    animating={submitting}
                />
                <ButtonBox enable={valid} onClick={handleSubmit(this._nextFun)}>下一步</ButtonBox>
            </Root>
        )
    }
}

const validateSupplyData = (value) => {
    const error = {};
    error.realname = required(value.realname);
    error.mobile = required(value.mobile);
    error.bankNum = required(value.bankNum);
    error.grade = required(value.grade);
    error.password = required(value.password);
    error.textarea = required(value.textarea);
    error.sex = required(value.sex);
    let _hobby = value.hobby;
    let valHobby = _hobby && _hobby.length > 0 ? _hobby : 0;
    error.hobby = required(valHobby);
    return error;
};

export default reduxForm({
    form: 'formData',
    validate: validateSupplyData,
})(FormData);

const Root = styled.div`
    width:100%;
    background-color:#fff;
`;

const FormBox = styled.div`
    padding: 0 .64rem;
    border-top: .1rem solid #f0f0f0;
    &&& {
        .am-list-item{
            margin-bottom: .3rem;
            border: .02rem solid #e1e1e1;
            border-radius: .06rem;
        }
        .am-list-line::after{height: 0}
    }
`;

const Label = styled.label`
    font-size: .28rem;
    color: #666666;
    display: block;
    padding: .34rem 0 .2rem;
`;

const ButtonBox = styled(Button)`
    margin: .8rem auto 0;
`;


