import * as api from '@js/networking/Api.web'

//提交表单
export const requestAccountData = (values: object,userId: string) => {
    return api.accountData(userId);
};

//查询用户列表
export const requestUsersList = () => {
    return async function() {
        const userId = await window.GoldWeb.getUserId();
        const model = api.getUserList(userId);
        return model;
    }
};

//获取拉动刷新数据列表
export async function requestPullRefreshData(page){
    const userId = await window.GoldWeb.getUserId();
    const model = await api.getPullRefreshData(userId, page);
    let list = model ? model.list : [];
    return {list: list}
}




