import React from 'react'

import {connect} from 'react-redux'

import styled from 'styled-components'

import {NavBar} from 'antd-mobile'

import MainTab from '../mainTab/index.web'

import needTokenPage from '@js/component/needTokenPage'

import {removeToken} from '@js/reducers/TokenActions'

class Account extends React.Component {

    constructor(props){
        super(props)
    }

    componentDidMount() {
        this._onRefresh()
            .catch((error) => {})
    }

    _onRefresh = async () => {
        return await window.GoldWeb.getUserId();
    };

    _jumpRoute = (type) => {
        if (type === 'Refresh') {
            this.props.history.push('/Account/Refresh');

        } else if (type === 'RefreshUpAndDown') {
            this.props.history.push('/Account/RefreshUpAndDown');

        } else if (type === 'FormData') {
            this.props.history.push('/Account/FormData');

        } else if (type === 'CDPage') {
            this.props.history.push('/Account/CDPage');

        } else if (type === 'UploadList') {
            this.props.history.push('/Account/UploadList')
        }
    };

    _loginOut = () => {
        this.props.removeToken();
        this.props.history.push('/Sys/');
    };

    render() {
        return(
            <Root>
                <NavBar mode="light">账户</NavBar>
                <ItemTitle>公共代码：</ItemTitle>
                <RowBox onClick={() => this._jumpRoute('Refresh')}><label>刷新组件</label></RowBox>
                <RowBox onClick={() => this._jumpRoute('RefreshUpAndDown')}><label>上下拉刷新</label></RowBox>
                <RowBox onClick={() => this._jumpRoute('FormData')}><label>表单组件</label></RowBox>
                <RowBox onClick={() => this._jumpRoute('CDPage')}><label>CD组件</label></RowBox>
                <RowBox onClick={() => this._jumpRoute('UploadList')}><label>上传组件</label></RowBox>
                <LoginOut onClick={this._loginOut}>退出登录</LoginOut>
                <MainTab {...this.props}/>
            </Root>
        )
    }
}

Account = connect(
    null,
    (dispatch) => ({
        removeToken: () => dispatch(removeToken())
    })
)(Account);

Account = needTokenPage(Account);

export default Account;

const Root = styled.div`
    width:100%;
    height:100%;
`;

const ItemTitle = styled.div`
    height: .8rem;
    line-height: .8rem;
    padding-left: .3rem;
`;

const RowBox = styled.div`
    height: .8rem;
    line-height: .8rem;
    background-color: #fff;
    margin-bottom: .1rem;
    padding: 0 .3rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    label{color: #999999}
    ::after{
        content: '';
        width: .16rem;
        height: .29rem;
        margin-top: .25rem; 
        background: url(${props => props.imgUrl || require('@js/resources/arrow-right@2x.png')})
    }
`;

const LoginOut = styled.button`
    width: 90%;
    height: .8rem;
    color: #fff;
    line-height: .8rem;
    display: block;
    margin: .6rem auto;
    background-color: #10b6f8;
    border-radius: .5rem;
`;


