import React from 'react'

import styled from 'styled-components'

import {Route, Switch} from 'react-router-dom'

import Account from './Account'
import Refresh from './Refresh'
import RefreshUpAndDown from './RefreshUpAndDown'
import FormData from './FormData'
import CDPage from './CDPage'

import UploadList from './UploadList'
import PhotoRadio from './PhotoRadio'
import PhotoCheckbox from './PhotoCheckbox'
import SupplementData from './SupplementData'



export default class extends React.Component {
    render() {
        return(
            <Root>
                <Switch location={this.props.location}>
                    <Route exact path={`${this.props.match.path}/`} component={Account}/>
                    <Route path={`${this.props.match.path}/Refresh`} component={Refresh}/>
                    <Route path={`${this.props.match.path}/RefreshUpAndDown`} component={RefreshUpAndDown}/>
                    <Route path={`${this.props.match.path}/FormData`} component={FormData}/>
                    <Route path={`${this.props.match.path}/CDPage`} component={CDPage}/>
                    <Route path={`${this.props.match.path}/UploadList`} component={UploadList}/>
                    <Route path={`${this.props.match.path}/PhotoRadio`} component={PhotoRadio}/>
                    <Route path={`${this.props.match.path}/PhotoCheckbox`} component={PhotoCheckbox}/>
                    <Route path={`${this.props.match.path}/SupplementData`} component={SupplementData}/>
                </Switch>
            </Root>
        )

    }
}

const Root = styled.div`
    width:100%;
    height:100%;
`;


