import React from 'react'

import styled from 'styled-components'

import {ActivityIndicator} from 'antd-mobile'

import {
    refreshAble
} from '@js/component/index.web'

import {
    requestUsersList
} from './reducers/AccountActions'


class Refresh extends React.Component {

    componentDidMount() {
        this._onRefresh()
    }

    componentWillUnmount() {
        this.props.fetchDataCancle()
    }

    _onRefresh = () => {
        this.props.fetchData(requestUsersList());
    };

    render() {
        let data = this.props.data;
        console.log(this.props)

        return(
            <div>
                <ButtonFixed>
                    <button onClick={this._onRefresh}>刷新</button>
                </ButtonFixed>
                <HeadBox>
                    <li>姓名</li>
                    <li>籍贯</li>
                </HeadBox>
                <RowList>
                    {
                        data && data.list.map((ele, index) => {
                            const {name, city} = ele;
                            return (
                                <RowBox key={index}>
                                    <li>{name}</li>
                                    <li>{city}</li>
                                </RowBox>
                            )
                        })
                    }
                </RowList>
                <ActivityIndicator
                    toast
                    text="Loading..."
                    animating={this.props.isRefreshing}
                />
            </div>
        )
    }
}

export default refreshAble('refreshDemo')(Refresh);

const ButtonFixed = styled.div`
    position: fixed;
    right: .5rem;
    bottom: .5rem;
    button {
        font-size: .25rem;
        display: block;
        width: .8rem;
        height: .8rem;
        line-height: .4rem;
        text-align: center;
        color: #333333;
        margin: 0 auto;
        border-radius: 50%;
        background-color: rgba(17, 182, 248, 0.5);
    }
`;

const HeadBox = styled.ul`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    background-color: #fff;
    width: 100%;
    display: flex;
    flex-direction: row;
    border-bottom: .01rem solid #e1e1e1;
    li{
        width: 50%;
        height: .8rem;
        line-height: .8rem;
        color: #333333;
        text-align: center;
    }
`;

const RowList = styled.div`
    margin-top: 1rem;
`;

const RowBox = styled.ul`
    background-color: #fff;
    display: flex;
    flex-direction:row;
    margin-bottom: .05rem;
    li {
        width: 50%;
        height: 1rem;
        line-height: 1rem;
        color: #999999;
        text-align: center;
    }
`;


