import React, {Component} from 'react'

import {connect} from 'react-redux'

import styled from 'styled-components'

import {Toast} from 'antd-mobile'

import {
    reduxForm,
    getFormValues,
    startSubmit,
    stopSubmit
} from 'redux-form'

import {
    Button
} from '@js/component/index.web'

import {Flex, ActivityIndicator} from "antd-mobile/lib/index";

import ImageSelect from '@js/component/uploadimage/ImageSelect.web'

import {required} from "@js/component/form/Validafunctions";

import {
    redux_upload,
    hasAllsccess,
    getUpdateItemID,
    getUploadData,
    getUploadedCount,
    getFailedCount,
    getWattingCount,
    getUpdatedIDs,
    upload,
    cleanUpload,
    pushUploadItem,
    deleteUploadItem,
    UploadEventEmitter
} from '@js/component/upload/index'

import lrz from 'lrz'

import * as api from '@js/networking/Api.web'


const PhotoRadioFormKey = 'photoRadioFormKey';
const PhotoRadioUploadKey = 'photoRadioUploadKey';


let UploadLoading = class extends Component {
    render() {
        return (
            <ActivityIndicator
                toast
                animating={this.props.show}
                text={this.props.totalCount > 0 ? `${this.props.uploadedCount}/${this.props.totalCount}` : ''}
            />
        )
    }
};

UploadLoading = connect(
    (state) => {
        const uploadedCount = getUploadedCount(state, PhotoRadioUploadKey);   //获取上传成功数量
        const wattingCount = getWattingCount(state, PhotoRadioUploadKey);   //获取等待上传数量
        const failedCount = getFailedCount(state, PhotoRadioUploadKey);     //获取失败上传数量
        const totalCount = uploadedCount + wattingCount + failedCount;
        return {
            uploadedCount: uploadedCount,
            totalCount: totalCount,
        }
    },
    null
)(UploadLoading);


let NextButton = class extends React.Component {

    uploadCompletesub: any;

    constructor() {
        super(...arguments)
    }

    componentDidMount() {
        this.uploadCompletesub = UploadEventEmitter.addCompleteListener((upload) => {
            if (this.props.uploadSuccess) {
                const {handleSubmit} = this.props;
                //因为不是事件触发的提交 handleSubmit(this._submit) 写法不会执行_submit函数，所以将_submit改为立即执行函数就会执行了
                handleSubmit(this._submit)();
            } else if (this.props.failedCount !== 0) {
                this.props.formStopSubmit({_error: '图片上传失败'});
                Toast.info('图片上传失败',1);
            }
        })
    }

    componentWillUnmount() {
        UploadEventEmitter.removeSubscription(this.uploadCompletesub);
        this.props.formStopSubmit({_error: '提交失败'});    //在上传过程中点返回停止提交
    }

    _onPress = () => {
        this.props.formStartSubmit();
        this.props.startUpload();
    };

    _submit = (() => {
        let submitting = false;
        //submitting redux-form提交成功与否的标示符
        //只有在表单提交时，返回一个promise的onSubmit函数，此prop（submitting）才会起作用
        //这里提交成功动态修改submitting的值
        const complete = () => submitting = false;

        return (values) => {
            if (submitting) return;
            submitting = true;
            //提交资料
            this._commit(values)
                .then((model) => {
                    complete();
                    console.log('测试1submitting==='+submitting);
                    this.props.formStopSubmit(null);
                    Toast.info(model.msg,3);
                })
                .catch((error) => {
                    complete();
                    console.log('测试3submitting==='+submitting);
                    this.props.formStopSubmit({_error: '提交失败'})
                })
        }
    })();
    //以上函数转化为ES5的写法，如下：js闭包(高阶函数里常用)+立即执行函数
    /*let _submit = function (){
       return tmp = function (values) {

       }
    }()*/

    _commit = async (values) => {
        console.log(values);
        const userId = await window.GoldWeb.getUserId();
        let material_data = {};
        material_data.xfFile = getUpdateItemID(this.props.imageUploadObj, 'xfFile');
        const model = api.uploadSupplement(userId, JSON.stringify(material_data));
        return model;
    };

    render() {
        const {valid} = this.props;

        return (
            <ButtonBox enable={valid} onClick={this._onPress}>立即登录</ButtonBox>
        )
    }
};

NextButton = connect(
    (state) => {
        const uploadedCount = getUploadedCount(state, PhotoRadioUploadKey);   //获取上传成功数量
        const wattingCount = getWattingCount(state, PhotoRadioUploadKey);   //获取等待上传数量
        const failedCount = getFailedCount(state, PhotoRadioUploadKey);     //获取失败上传数量
        const totalCount = uploadedCount + wattingCount + failedCount;
        return {
            uploadSuccess: hasAllsccess(state, PhotoRadioUploadKey),   //判断是否上传完成
            imageUploadObj: getUploadData(state, PhotoRadioUploadKey),  //获取上传对象
            uploadedCount: uploadedCount,
            totalCount: totalCount,
            failedCount: failedCount,
        }
    },
    (dispatch) => ({
        //调用redux-form的startSubmit方法开始提交
        formStartSubmit: () => dispatch(startSubmit(PhotoRadioFormKey)),   //注意这里key的指定

        //调用redux-form的stopSubmit方法停止提交
        formStopSubmit: (error) => dispatch(stopSubmit(PhotoRadioFormKey, error))
    })
)(NextButton);



class PhotoRadio extends React.Component {

    render() {
        const {submitting} = this.props;
        console.log('测试2submitting==='+submitting);

        const image3 = `url(${require('./resources/photo.png')})`;

        return (
            <SuppleBox>
                <h3>请您按要求完善资料：</h3>
                <SuppleForm>
                    <UploadBox>
                        <Flex direction='row' align='baseline' style={{paddingTop: '0.3rem', paddingBottom: '0.3rem'}}>
                            <ArountBox/>
                            <CommonText style={{color: '#666666'}}>{'消费凭证'}</CommonText>
                        </Flex>
                        <ImageSelect
                            formKey={PhotoRadioFormKey}
                            image={image3}
                            name={'xfFile'}
                            imagename={'消费照片'}
                            pushUploadItem={this.props.pushUploadItem}
                            deleteUploadItem={this.props.deleteUploadItem}
                            {...this.props}/>
                    </UploadBox>
                </SuppleForm>
                {/*提交成功submitting变为false*/}
                <UploadLoading show={submitting}/>
                <NextButton {...this.props}/>
            </SuppleBox>
        )
    }
}

PhotoRadio = redux_upload({
    upload: PhotoRadioUploadKey,
    uploadApi: api.uploadImageWithPost,
    getUUIDFuc: ((model) => model.uuid),   //调用上传接口成功后拿到uuid
    isSuccess: ((model) => model.success)
})(PhotoRadio);

const validateForm = (value) => {
    const error = {};
    error.xfFile = required(value.xfFile);
    return error;
};

PhotoRadio = reduxForm({
    form: PhotoRadioFormKey,
    validate: validateForm
})(PhotoRadio);

export default PhotoRadio



const ButtonBox = styled(Button)`
    width: 90%;
    margin: .6rem auto;
`;

const CommonText = styled.p`
    margin-left: 0.3rem;
    font-size:0.28rem;
    color:#999999;
`

const SuppleBox = styled.div`
    width: 100%;
    height: 100%;
    h3 {
        font-size: .3rem;
        color: #333333;
        padding: .35rem .3rem;
    }
`;

const SuppleForm = styled.div`
    background-color: #fff;
    &&& {
        .am-textarea-item{width:100%;}
    }
`;

const ArountBox = styled.div`
    content:'';
    display: block;
    width:.2rem;
    height:.2rem;
    border-radius:50%;
    -webkit-border-radius:50%;
    background-color: #11b6f8;
    margin: 0 0.16rem 0 0.16rem;
`;

const RowFlex = styled(Flex).attrs({
    direction: 'row',
    align: 'center',
})`
  width: 100%;
  border-color: #e1e1e1;
  border-bottom-width: 0.03rem;
  border-bottom-style:solid;ss
`

const UploadBox = styled(RowFlex).attrs({
    justify: 'between',
})`
    height: 1.76rem !important;
    padding-right: 0.16rem;
    div.am-flexbox-align-center {
        width:1rem;height:1rem;
    };
    div.am-image-picker-upload-btn:before{
        width:1rem;
        height:1rem;
        border: 0;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100%;
        background-color: #fff;
    }
    div.am-image-picker-upload-btn:after{
        border:0;
        background-color: #fff;
    }
    div.am-image-picker-list{padding:0;margin:0}
    div.am-flexbox-item:first{
        width:1rem;
        height:1rem;
        position: absolute;
        top: 0.2rem;
        left: 0;
        z-index: 100;
    }
    div.am-image-picker-item{width:1rem;height:1rem;}

`


