//@flow

//验证函数

//必填
export const required = (value: string) => (value ? undefined : '必填')

//最大长度
export const maxLength = (max: number) => (value: string) =>
    value && value.length > max ? `必须要小于或等于${max}字符` : undefined

//最大长度15
export const maxLength15 = maxLength(15)

//最小长度
export const minLength = (min: number) => (value: string) =>
    value && value.length < min ? `必须要大于或等于${min}字符` : undefined

//最小长度2
export const minLength2 = minLength(2)

//必须是数字
export const number = (value: string) =>
    value && isNaN(Number(value)) ? 'Must be a number' : undefined

//最小值
export const minValue = (min: number) => (value: string) =>
    value && !isNaN(Number(value)) && Number(value) < min ? `Must be at least ${min}` : undefined

//最小值是18
export const minValue18 = minValue(18)

//邮件
export const email = (value: string) =>
    value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)
        ? '无效的邮件地址'
        : undefined

//不能大于65
export const tooOld = (value: string) =>
    value && !isNaN(Number(value)) && Number(value) > 65 ? 'You might be too old for this' : undefined


//手机号码
export const phoneNumber = (value: string) =>
    value && !/^(0|[1-9][0-9]{10})$/i.test(value)
        ? '无效的手机号码，必须是11位数字'
        : undefined

//判断身份证
export const idNumber = (value: string) =>
    value && value.length != 18
        ? '请输入正确的身份证号'
        : undefined

//手机号码去除空格
export const phoneNumberNoSpace = (value: string) =>
    value && !/^(0|[1-9][0-9]{10})$/i.test(value.replace(/\s+/g, ""))
        ? '无效的手机号码，必须是11位数字'
        : undefined

//判断11位手机号码并去除空格
export const _phoneNumber = (value: string) => {
    var reg = /^(0|[1-9][0-9]{10})$/i;

    if (reg.test(value.replace(/\s+/g, ""))) {
        return true
    } else {
        return false
    }

};

//判断姓名格式
export const regName = (value: string) =>
    value && !/^[\u4e00-\u9fa5]{2,4}$/.test(value) ? '真实姓名填写有误' : undefined;

//验证银行卡号
export const checkLuhm = (bankno: number) => {
    var result_text;

    if (bankno) {
        var lastNum=bankno.substr(bankno.length-1,1);//取出最后一位（与luhm进行比较）

        var first15Num=bankno.substr(0,bankno.length-1);//前15或18位
        var newArr=new Array();
        for(var i=first15Num.length-1;i>-1;i--){    //前15或18位倒序存进数组
            newArr.push(first15Num.substr(i,1));
        }
        var arrJiShu=new Array();  //奇数位*2的积 <9
        var arrJiShu2=new Array(); //奇数位*2的积 >9

        var arrOuShu=new Array();  //偶数位数组
        for(var j=0;j<newArr.length;j++){
            if((j+1)%2==1){//奇数位
                if(parseInt(newArr[j])*2<9)
                    arrJiShu.push(parseInt(newArr[j])*2);
                else
                    arrJiShu2.push(parseInt(newArr[j])*2);
            }
            else //偶数位
                arrOuShu.push(newArr[j]);
        }

        var jishu_child1=new Array();//奇数位*2 >9 的分割之后的数组个位数
        var jishu_child2=new Array();//奇数位*2 >9 的分割之后的数组十位数
        for(var h=0;h<arrJiShu2.length;h++){
            jishu_child1.push(parseInt(arrJiShu2[h])%10);
            jishu_child2.push(parseInt(arrJiShu2[h])/10);
        }

        var sumJiShu=0; //奇数位*2 < 9 的数组之和
        var sumOuShu=0; //偶数位数组之和
        var sumJiShuChild1=0; //奇数位*2 >9 的分割之后的数组个位数之和
        var sumJiShuChild2=0; //奇数位*2 >9 的分割之后的数组十位数之和
        var sumTotal=0;
        for(var m=0;m<arrJiShu.length;m++){
            sumJiShu=sumJiShu+parseInt(arrJiShu[m]);
        }

        for(var n=0;n<arrOuShu.length;n++){
            sumOuShu=sumOuShu+parseInt(arrOuShu[n]);
        }

        for(var p=0;p<jishu_child1.length;p++){
            sumJiShuChild1=sumJiShuChild1+parseInt(jishu_child1[p]);
            sumJiShuChild2=sumJiShuChild2+parseInt(jishu_child2[p]);
        }
        //计算总和
        sumTotal=parseInt(sumJiShu)+parseInt(sumOuShu)+parseInt(sumJiShuChild1)+parseInt(sumJiShuChild2);

        //计算Luhm值
        var k= parseInt(sumTotal)%10==0?10:parseInt(sumTotal)%10;
        var luhm= 10-k;


        if(lastNum==luhm){
            return result_text;
        }
        else{
            result_text = '银行卡号有误';
            return result_text;
        }
    }
};


