import React from 'react'

import styled from 'styled-components'

import {NavBar} from 'antd-mobile'

class UploadList extends React.Component {

    constructor(props){
        super(props)
    }

    _jumpRoute = (type) => {
        if (type === 'PhotoRadio') {
            this.props.history.push('/Account/PhotoRadio');

        } else if (type === 'PhotoCheckbox') {
            this.props.history.push('/Account/PhotoCheckbox');

        } else if (type === 'SupplementData') {
            this.props.history.push('/Account/SupplementData');
        }
    };

    render() {
        return(
            <Root>
                <NavBar mode="light">上传Demo</NavBar>
                <RowBox onClick={() => this._jumpRoute('PhotoRadio')}><label>单选</label></RowBox>
                <RowBox onClick={() => this._jumpRoute('PhotoCheckbox')}><label>多选</label></RowBox>
                <RowBox onClick={() => this._jumpRoute('SupplementData')}><label>单选+多选组合</label></RowBox>
                <Text>相关说明：参考<a target="_blank" href="https://github.com/aichen28/react_template/blob/master/doc/upload.md">上传Demo</a></Text>
            </Root>
        )
    }
}

export default UploadList;

const Root = styled.div`
    width:100%;
    height:100%;
`;

const RowBox = styled.div`
    height: .8rem;
    line-height: .8rem;
    background-color: #fff;
    margin-bottom: .1rem;
    padding: 0 .3rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    label{color: #999999}
    ::after{
        content: '';
        width: .16rem;
        height: .29rem;
        margin-top: .25rem; 
        background: url(${props => props.imgUrl || require('@js/resources/arrow-right@2x.png')})
    }
`;

const Text = styled.p`
    color: #999999;
    line-height: .4rem;
    text-indent: .3rem;
    a{color: #11b6f8;}
`;




