import React from 'react'

import styled from 'styled-components'

import {connect} from 'react-redux'

import {bindActionCreators} from 'redux'

import {
    PullDownRefreshListWeb,
    PullDownRefreshListActions
} from '@js/component/index.web'

import {
    requestPullRefreshData
} from './reducers/AccountActions'

class Item extends React.Component {

    constructor(props) {
        super(props)
    }

    render() {
        const {image, csentence, money} = this.props;

        return (
            <div
                 style={{
                     padding: '0 .3rem',
                     backgroundColor: 'white',
                 }}
            >
                <div style={{ display: '-webkit-box', display: 'flex', padding: '.3rem' }}>
                    <img style={{ height: '1.28rem', width: '1.28rem', marginRight: '.6rem' }} src={image} alt="" />
                    <div style={{ display: 'inline-block' }}>
                        <div style={{ marginBottom: '.16rem', color: '#000', fontSize: '.32rem', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap', maxWidth: '5rem' }}>{csentence}</div>
                        <div style={{ fontSize: '.32rem' }}><span style={{ fontSize: '.6rem', color: '#FF6E27' }}>¥{money}</span>元</div>
                    </div>
                </div>
            </div>
        )
    }
}

class RefreshUpAndDown extends React.Component {
    render() {
        const row = (rowData, sectionID, rowID) => {
            return (
                <Item {...rowData} {...this.props} key={`${sectionID}-${rowID}`}/>
            )
        };

        return(
            <RefreshBox>
                <PullDownRefreshListWeb
                    style={{width: '100%', height: '100%'}}
                    ref={el => this.lv = el}
                    renderRow={row}
                    useBodyScroll={false}
                    asyncApi = {requestPullRefreshData}
                    identifier = "RefreshUpAndDown"
                    noDataRender = {(
                        <div style={{display:'flex',justifyContent:'center',alignItems:'center',flexDirection:'column',height: '100%'}}>
                            <p style={{fontSize:'0.3rem',color:'#666666',marginTop:'0.6rem'}}>暂无账单记录</p>
                        </div>
                    )}
                />
            </RefreshBox>
        )
    }
}

export default connect(null, (dispatch) => bindActionCreators(PullDownRefreshListActions, dispatch))(RefreshUpAndDown)


const RefreshBox = styled.div`
    width: 100%;
    height: 100%;
`;


