import React, {Component} from 'react';
import styled from 'styled-components'
import {connect} from 'react-redux'
import {cleanUpload} from "../upload";
import { ImagePicker} from 'antd-mobile';
import PropTypes from "prop-types";
import lrz from "lrz";


let UploadKey = '';

class MutImageSelect extends Component {

    static propTypes = {
        uploadKey: PropTypes.string
    };

    constructor() {
        super(...arguments);
        this.state = {
            files: []
        };
        UploadKey = this.props.uploadKey;
    }

    onChange = (files, type, index) => {
        this.setState({
            files,
        });

        //每次onChange图片，先清除当前FormKey中到所有图片，再将变化的files重新添加到等待上传队列
        this.props.cleanUpload();

        for (let index = files.length - 1; index >= 0; index--) {
            const file = files[index]
            this.props.pushUploadItem(file.url, `mutImage_${index}`)
        }
    };

    render() {
        const {files} = this.state;
        return (
            <div style={{height: '100%',}}>
                <h3 style={{fontSize: '0.3rem', color: '#666666', fontWeight: 'normal', paddingTop: '0.2rem'}} >
                    <CommonText style={{color: '#666666'}}>补充其他资料</CommonText>
                </h3>

                <div style={{width: '100%', float: 'none', overflow: 'visible'}} className="edmoney_infosure_imageUpload">
                    <div style={{marginLeft: '0.2rem'}}>
                        <ImagePicker
                            files={files}
                            onChange={this.onChange}
                            onImageClick={(index, fs) => console.log(index, fs)}
                            selectable={files.length < 10}
                            //multiple={true}
                        />
                    </div>

                </div>

                <Divide></Divide>
            </div>
        )
    }

}

MutImageSelect = connect(
    null,
    (dispatch) => ({
        cleanUpload: () => dispatch(cleanUpload({upload: UploadKey}))   //调用高阶组件中的cleanUpload方法
    })
)(MutImageSelect);

export default MutImageSelect

const CommonText = styled.p`
    font-size:0.28rem;
    color:#999999;
`
const Divide = styled.div`
    height:0.03rem;
    background-color:#e1e1e1;
    width:100%;
`



